import React from 'react'
import Presentation from './presentation'

class Container extends React.Component{
    state = {
        youtube : "UCNLm0XtW8zWuzmhD5BqXagw",
        twitch : "developerswork",
        mixer : "",
        facebook : "",
        player : {
            height : "60vh",
            width : "100%",
            type : "youtube"
        },
        chat : {
            height : "60vh",
            width : "80%",
            url : 'https://chat.restream.io/embed?token=bd031f04-e345-4529-ac76-6d73ec823c9a'
        }
    }

    getChat = () => {
        return(
            <iframe
                src={this.state.chat.url}
                width={this.state.chat.width}
                style={{height:this.state.chat.height,backgroundColor:"black"}}
            >
            </iframe>
        )
    }

    getPlayer = () => {
        switch(this.state.player.type){
            case "youtube":
                return this.getYouTubePlayer()
            case "twitch":
                return this.getTwitchPlayer()
        }
    }

    getYouTubePlayer = () => {
        return (
            <iframe 
                width={this.state.player.width}
                style={{height:this.state.player.height}}
                src={"https://www.youtube.com/embed/live_stream?channel=" + this.state.youtube} 
                frameborder="0" 
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                allowfullscreen>
            </iframe>
        )
    }

    getTwitchPlayer = () => {
        return (
            <iframe
                src={"https://player.twitch.tv/?channel=" + this.state.twitch + "&parent=streamernews.example.com"}
                width={this.state.player.width} 
                height={this.state.player.height}
                frameborder="0"
                scrolling="no"
                allowfullscreen="true">
            </iframe>
        )
    }

    update = e => {
        switch(e.target.name){
            case "platform":
                this.setState({
                    player : {
                        ...this.state.player,
                        type : e.target.value
                    }
                });
                break
        }
    }

    render(){
        return (
            <Presentation
                update={this.update}
                player={this.getPlayer}
                chat={this.getChat}
                platform={this.state.player.type}
            />
        )
    }
}

export default Container